header {
  height: 100vh;
  padding-top: 7rem;
  overflow: hidden;
}
.header__container {
  text-align: center;
  height: 100%;
  position: relative;
}

/* ************************* CTA ************ */
.cta {
  display: flex;
  gap: 1.2rem;
  margin-top: 2.5rem;
  justify-content: center;
}

/* ************************* HEADER SOCIALS ************ */
.header__socials {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.8rem;
  left: 0;
  position: absolute;
  bottom: 3rem;
}
.header__socials::after {
  content: "";
  width: 1px;
  height: 2rem;
  background: var(--color-primary);
}

.me {
    background:linear-gradient(var(--color-primary),transparent);  
    width:22rem;
    height:30rem;
    position:absolute;
    left:calc(50% - 11rem);
    margin-top:4rem;
    border-radius:10rem 10rem 0 0;
    overflow: hidden;
    padding: 1rem 1rem 1rem;
}

/*  SCROLL DOWN */
.scroll__down {
    position:absolute;
    right: -2.3rem;
    bottom:5rem;
    transform:rotate(90deg);
    font-weight:300;
    font-size:0.9rem;
}


/*========================= MEDIA QUERIES ( MEDIA DEVICES) ======================= */
@media screen and (max-width: 1024px) {
.container{
   
}
.header{
    height:68vh;
}
}

@media screen and (max-width: 600px) {
.header{
    height:100vh;
}
.header__socials,.scroll__down{
    display:none;

}
}
